import React, { Component } from "react";
import PropTypes from "prop-types";
import {
  Accordion,
  Icon,
  Segment,
  Header,
  Button,
  Divider,
  Image,
  List,
  Grid
} from "semantic-ui-react";
import ImageGallery from "react-image-gallery";

import { Container } from "reactstrap";
import galleries from "../data/galleries";

export default class SubGalleryPage extends Component {
  state = { activeIndex: 0 };

  handleClick = (e, titleProps) => {
    const { index } = titleProps;
    const { activeIndex } = this.state;
    const newIndex = activeIndex === index ? -1 : index;

    this.setState({ activeIndex: newIndex });
  };

  gallery = galleries.find(
    item => item.name === this.props.match.params.galleryName
  );
  render() {
    const { activeIndex } = this.state;
    const imagesLoad = require.context("../img/gallery/footage", false, /\.(png|jpe?g|svg)$/);
    function importAll(r) {
      const images = [];
      r.keys().forEach((item, i) => {
        images.push({
          original: r(item),
          thumbnail: r(item)
        })
      });

      // r.keys().map((item, index) => {
      //   images[item.replace("./", "")] = r(item);
      // });
      return images;
    }
    const images = importAll(imagesLoad);
    // const images = importAll(
    //   require.context("../img/gallary", false, /\.(png|jpe?g|svg)$/)
    // );
    return (
      <Segment style={{ padding: "4em 0em" }} vertical>
        <Container text>
          <Header as="h3" style={{ fontSize: "2em" }}>
            {this.gallery.name}
          </Header>
          <Accordion fluid styled>
            <Accordion.Title
              active={activeIndex === 0}
              index={0}
              onClick={this.handleClick}
            >
              <Icon name="dropdown" />
              Description
            </Accordion.Title>
            <Accordion.Content active={activeIndex === 0}>
              <p>
                {this.gallery.description
                  ? this.gallery.description
                  : this.gallery.shortDescription}
              </p>
            </Accordion.Content>
          </Accordion>

          <Divider
            as="h4"
            className="header"
            horizontal
            style={{ margin: "3em 0em", textTransform: "uppercase" }}
          >
            <a>OVERVIEW</a>
          </Divider>
          <ImageGallery items={images} />

          <Header as="h3" style={{ fontSize: "2em" }}>
            Panupong Leenawarat
          </Header>
          <div>
            <Button circular color="facebook" icon="facebook" />
            <Button circular color="twitter" icon="twitter" />
            <Button circular color="linkedin" icon="linkedin" />
            <Button circular color="google plus" icon="google plus" />
          </div>
          <List>
            <List.Item>
              <List.Icon name="world" />
              <List.Content>Origin Thailand</List.Content>
            </List.Item>
            <List.Item>
              <List.Icon name="marker" />
              <List.Content>Amherst, MA</List.Content>
            </List.Item>
            <List.Item>
              <List.Icon name="mail" />
              <List.Content>
                <a href="mailto:panupong_lee@me.com">panupong_lee@me.com</a>
              </List.Content>
            </List.Item>
            <List.Item>
              <List.Icon name="phone" />
              <List.Content>
                <a href="tel:+18572043916">8572043916</a>
              </List.Content>
            </List.Item>
          </List>
        </Container>
      </Segment>
    );
  }
}

SubGalleryPage.propTypes = {
  match: PropTypes.string.isRequired
};
